.social .col-md-3 p {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 10px;
  width: 100%;
}
.social .col-md-3 p.instagram {
  background: #E4405F;
